@charset "utf-8";
@font-face {
  font-family: "Escoredream";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-4Regular.woff")
    format("woff");
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: "Escoredream";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-5Medium.woff")
    format("woff");
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: "Escoredream";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-6Bold.woff")
    format("woff");
  font-weight: 600;
  font-display: swap;
}

@font-face {
  font-family: "Escoredream";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-7ExtraBold.woff")
    format("woff");
  font-weight: 700;
  font-display: swap;
}

* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
ol,
ul {
  list-style: none;
}
#root {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(./images/desktop_bg.png);
  background-size: cover;
  font-family: "Escoredream";
}
/* app */
#app {
  background-image: url(./images/app_bg1.png);
  width: 360px;
  height: 640px;
  border: 4px solid #fff;
  border-radius: 1rem;
  box-shadow: 6px 0 8px -4px rgba(0, 0, 0, 0.2),
    0 6px 8px -4px rgba(0, 0, 0, 0.2);
}
#categories {
  ul {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      cursor: pointer;
      margin: 0.5rem 0;
      border-radius: 2rem;
      padding: 10px 0;
      width: 187px;
      height: 41px;
      background-color: #fff;
      color: #ff9ec5;
      font-weight: 700;
      font-size: 1.1rem;
      line-height: 1.2;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 4px 8px rgba(255, 158, 197, 0.4);
      border: none;
      transition: filter 0.3s ease, box-shadow 0.3s ease;

      &:hover {
        color: #FF71AA;
      }
      &:active {
        filter: brightness(0.85);
        box-shadow: 0 2px 6px rgba(255, 158, 197, 0.8);
      }
    }
  }
}

@media all and (max-width: 360px) {
  #app {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 0;
  }
}
